#job-page {
  padding-top: 80px;
  .job-title {
    margin-bottom: 10px;
  }
  .job-location {
    margin-bottom: 20px;
  }
  .job-desc-title {
    margin-bottom: 5px;
    font-size: 1.5rem;
  }

  h4,
  h5 {
    text-transform: unset;
  }
  p {
    margin-bottom: 20px;
  }
  ul {
    list-style-type: disc;
    padding-inline-start: 30px;
    margin-bottom: 30px;
    li {
      color: var(--text-light);
    }
  }
}
@media (min-width: 768px) {
  #job-page {
    padding-top: 100px;
  }
}

@media (min-width: 992px) {
  #job-page {
    padding-top: 120px;
  }
}
