.theme-toggle-wrapper {
  cursor: pointer;
  display: flex;
  position: fixed;
  transform: rotate(-90deg);
  top: 50%;
  z-index: 103;
  left: 20px;
  justify-content: space-between;
  background: #e8e8e8;
  border-radius: 20px;

  &.mobile {
    position: relative;
    left: 0;
    transform: translateY(100%);
    opacity: 0;
    pointer-events: none;

    .toggler {
      position: absolute;
    }
  }
  input[type="checkbox"] {
    opacity: 0;
    position: absolute;
    &:checked + .theme-toggle .toggle {
      transform: translateX(0);
    }
  }
  .theme-toggle {
    cursor: pointer;
    width: 100px;
    display: flex;
    justify-content: space-between;
    position: relative;
    padding: 5px 10px;
    span {
      display: inline-block;
      color: #000;
      z-index: 9;
    }
    .toggle {
      background: #fff;
      transform: translateX(100%);
      position: absolute;
      width: 50px;
      height: 24px;
      top: 2px;
      left: 0;
      box-shadow: -1px 0 4px rgba(23, 22, 26, 0.05);
      border-radius: 20px;
      transition: transform 0.5s ease-in-out;
    }
  }
}
